const notify = (msg, duration = 3000, destination = "", style = {
    background: "#041115",
    border: "2px solid white"
}, callback) => {
    const toast = Toastify({
        text: msg,
        className: "Toastify__toast--success",
        duration: duration,
        destination: destination,
        newWindow: true,
        avatar: 'success.ae358123.svg',
        close: true,
        gravity: "top", // `top` or `bottom`
        position: "right", // `left`, `center` or `right`
        stopOnFocus: true, // Prevents dismissing of toast on hover
        style: '.toast__sucess',
        onClick: callback// Callback after click
    });

    function show() {
        toast.showToast();
    }

    return show;
}

// const toastSuccess = notify(msg);
// toastSuccess();
function toastSuccess(msg, duration = 3000) {
    console.log(duration);
    notify(msg, duration)();
}

